async function addQuote() {
  let contactName = document.getElementById("contactName").value.substring(0, 500);
  let contactPhone = document.getElementById("contactPhone").value.substring(0, 500);
  let contactEmail = document.getElementById("contactEmail").value.substring(0, 500);
  let contactMessage = document.getElementById("contactMessage").value.substring(0, 5000);
  let date = new Date();
  date.setHours(date.getHours() + 10);
  let d = date.toISOString();
  console.log("d is " + d);
  if (contactName !== "" && contactEmail !== "" && contactPhone !== "" && contactMessage !== "") {  
    console.log("send it");
    await firebase.addQuote(contactName, contactPhone, contactEmail, contactMessage, d);
  }
}

function onClick(n) {
  if (document.getElementById("img01")) {
    if (n === 1) document.getElementById("img01").src = p1;
    else if (n === 2) document.getElementById("img01").src = p2;
    else if (n === 3) document.getElementById("img01").src = p3;
    else if (n === 4) document.getElementById("img01").src = p4;
    else if (n === 5) document.getElementById("img01").src = p5;
    else if (n === 6) document.getElementById("img01").src = p6;
    else if (n === 7) document.getElementById("img01").src = p7;
    else if (n === 8) document.getElementById("img01").src = p8;
    else document.getElementById("img01").src = "./img/p1.jpg";
    document.getElementById("modal01").style.display = "block";
  } else {
  }
  /*var captionText = document.getElementById("caption");
  captionText.innerHTML = element.alt;*/
}


